<template>
  <div id="app">
    <img src="./assets/logo.png" >
    <div class="start">
      <!-- <el-button @click="startHacking">Start</el-button> -->
       <el-button @click="getImages">getImages</el-button>
         <el-button @click="show">ImageViewer</el-button>
    </div>

 

    <el-row :gutter="20">
      <el-col :span="12"  v-for="(val, key) in images"  :key="key"> 
        
       <el-card :body-style="{ padding: '0px' }">
         <div class="box">
            <img v-bind:src="val.image" class="image">
         </div> 
      <div class="source">
        <a href="javascript:void(0)" @click="goSource(val.sourceUrl)">{{ val.title }}</a>
        </div>
       

      <div class="username-info">  
        <img :src="val.avatar">
        <span>{{ val.username }}</span> 
      </div>
      
    </el-card>
    </el-col>
    </el-row>
    <el-button v-if="isMore" @click="more">more</el-button>
     
     <div class="image-viewer" :class="{ 'image-viewer-hidden' : isHide}">
        <h1>看图模式 </h1>
        <img class="close" src="./assets/close.png" @click="show">
        <viewer :images="images">
            <img v-for="src in images" :src="src.image" :key="src.id">
        </viewer>
   </div>
  </div>
</template>

<script>
import Vue from 'vue'
export default {
  data() {
    return { 
      images: [],
      isMore: false,
      page: 1,
      count: 20,
      isHide:true
    }
  },
  methods: {
    startHacking () {
      this.$notify({
        title: 'It works!',
        type: 'success',
        message: 'We\'ve laid the ground work for you. It\'s time for you to build something epic!',
        duration: 5000
      })
    },
    getImages(){
      Vue.axios.get('http://www.popochiu.com/hupu/getImages').then(res=>{
        console.log(res)
        this.isMore = res.data.length == this.count;
        for(let item of res.data){
          item.images = JSON.parse( item.images)
          item.images.forEach((element,index) => { 
            if(element.indexOf('placeholder') == -1){
              let object = {
              id: item.articleid + index,
              avatar: item.avatar,
              sourceUrl: JSON.parse(item.sourceUrl),
              title: item.title,
              username: item.username,
              image: element
            }
              this.images.push(object)
            }
          })  
        } 
      })

    },
    more(){ 
      let offset = this.page * this.count
       Vue.axios.get(`http://www.popochiu.com/hupu/getImages?offset=${offset}`).then(res=>{ 
         this.isMore = res.data.length == this.count;
         this.page ++;
        for(let item of res.data){
          item.images = JSON.parse( item.images)
          item.images.forEach((element,index) => {
            
            if(element.indexOf('placeholder') == -1){
              let object = {
              id: item.articleid + index,
              avatar: item.avatar,
              sourceUrl: JSON.parse(item.sourceUrl),
              title: item.title,
              username: item.username,
              image: element
            }
              this.images.push(object)
            }
          })  
        }  
      })
    },
    goSource(url){
      window.open(url)
    },
    show(){
      this.isHide = !this.isHide;
      if(!this.isHide){ 
        location.href = location.href + 'showImages'
      }else{
         window.history.go(-1)
         
      }
    },
    listenHistory(e){  
       this.isHide = !location.href.split('#')[1]
    }
  },mounted(){
    location.href += '#' 
    if (window.history && window.history.pushState) {
      history.pushState(null, null, document.URL);
      window.addEventListener('popstate', this.listenHistory, false);
    }
  }
 
}

  
</script>

<style>
#app {
  font-family: Helvetica, sans-serif;
  text-align: center;
}

.close{
  position: fixed;
  right:10px;
  top:10px;
  width: 1.5rem;
}
.image-viewer{
  position: absolute;
  top: 0; 
  bottom:0;
  left:0;
  right:0;
  z-index: 5;
  background-color: #000;
  transform: translateX(0%);
  transition: all .2s;
  transform-origin:100% 0;
}

.image-viewer-hidden{ 
  transform:  translateX(-100%);
}
.image-viewer h1{
  background: #000;
  margin:0;
  padding: 16px 0;
  color: #ccc;
  position: fixed;
  top:0;
  width: 100%;

}

.image-viewer div{
  background: #fff; 

}
img{
  max-width: 100%;
}
.start{
  padding: 20px; 
}

.box{
  padding-bottom: 132%;
  position: relative;
  overflow: hidden;
}

.box img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%)
}
.username-info{
  padding: 8px 16px ;
  display: flex;
  align-items: center;
  
}

.username-info img{
  border-radius: 50%;
  width: 1.5rem;
}

.username-info span{
  padding-left: 8px;
  text-align: left;
  font-size: 1rem;
}

.source{
  padding:8px 16px ;
  text-align: left;
}

.source a,p{ 
  min-height:50px;
   overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box; 
    -webkit-line-clamp: 2;
    /* autoprefixer: off; */
  -webkit-box-orient: vertical;
  /* autoprefixer: on ; */
 
}
</style>



